import { Box } from "../../../core/MyBox";
import { MyFormItem } from "../../../core/Input";
import { useState } from "react";
import { MyButton } from "../../../core/MyButton";
import { SettingService } from "../../../../services/taskService";
import { useParams } from "react-router-dom";
import { Fn } from "../../../../helper/function";

export default function ImportFileComponent(props: any) {
    const [file, setFile] = useState<string | Blob>();
    const projectKey = useParams().key;
    function handleOnClick() {
        console.log(file);
        doUpload();
    }

    function doUpload() {
        props.setIsLoading(true);
        const formData = new FormData();
        if (file) {
            formData.append("File", file);
        }

        SettingService.Import(projectKey, formData, (data: any) => {
            console.log(data);
            Fn.PrintMsgSuccess(data);
        }, undefined, () => {
            props.setIsLoading(false);
        })
    }

    function handleOnClickExport() {
        props.setIsLoading(true);
        SettingService.Export(projectKey, (data: any) => {
            var blob = new Blob([data.data], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
            console.log(blob);

            var url = URL.createObjectURL(blob);
            window.open(url);
        }, undefined, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Box>
            <MyButton
                type="primary"
                onClick={handleOnClickExport}
                value="Export file" />

            <MyButton
                type="success"
                onClick={handleOnClick}
                value="Import file"
                disabled={file === undefined} />

            <MyFormItem.File
                value={file}
                setValue={setFile} />
        </Box>
    )
}